import React from "react";

export const Subscribe = () => {
  return (
    <div className="subs">
      <div className="subs-head">
        <div className="subs-head-text">Stay Updated</div>
        <div className="line"></div>
      </div>
      <p className="subs-para">
        Subscribe To Our Daily Newsletter And Get News Delivered Straight To
        Your Inbox.
      </p>
      <div className="subs-form">
        <div className="inp-sec">
          <input type="email" placeholder="Email" className="inp"></input>
        </div>
        <button type="button" className="subs-btn">
          Subcribe
        </button>
      </div>
      <span class="subscribers">Join over 120,000 subscribers!</span>
    </div>
  );
};
