import React from "react";

export const StoryCard = (props) => {
  if (props.type === "blurb") {
    return (
      <>
        <div className="story-element-text-blurb">
          <div dangerouslySetInnerHTML={{ __html: props.content }}></div>
        </div>
      </>
    );
  }
  return (
    <>
      <div className="story-element-text">
        <div dangerouslySetInnerHTML={{ __html: props.content }}></div>
      </div>
    </>
  );
};
